.skills {
  position: relative;
  grid-area: s;
}

.skills__title {
  margin-bottom: 10px;
}

.skills__title--other {
  margin: 0;
  margin-right: 30px;
  padding-top: 10px;
}

.skills__certificate {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.skills__other {
  margin-top: 10px;
  margin-bottom: 40px;
}

.skills__list {
  margin: 0;
  margin-bottom: 10px;
  padding: 0;
}

.skills__wrapper {
  display: flex;
  justify-content: baseline;
  margin-bottom: 20px;
}
